'use strict'

import lightGallery from 'lightgallery';
import * as functions from "./modules/functions.js"
import { ibg } from "./modules/ibg.js";
import { validateForm } from "./modules/validationForms.js";

document.addEventListener('DOMContentLoaded', (e) => {
	//image by background
	ibg();
	//burgerMenu
	functions.burgerMenu();
	//custom fullscreen for IE
	functions.fullScreenIE();
	functions.isWebp();
	validateForm();

	//menu burgerMenuСolor
	const burgerButton = document.querySelector('.header__burger');
	burgerButton.addEventListener('click', () => {
		const listWrapper = document.querySelectorAll('[data-lineEffect]');
		if (window.matchMedia("(max-width: 767.98px)").matches) {
			listWrapper.length ? burgerColor(listWrapper) : null;
		}
	});

	function burgerColor(listWrapper) {
		listWrapper.forEach(list => {
			const links = list.querySelectorAll('a');
			const effectSpeed = list.dataset.lineEffect ? list.dataset.lineEffect : 0.4;
			links ? startEffect(links, effectSpeed) : null;
		});
	}

	function startEffect(links, effectSpeed) {
		const effectTransition = `transition: transform ${effectSpeed}s ease;`;
		const effectHover = `transform: translate3d(0%, 0px, 0px);`;
		const effectleft = `transform: translate3d(-100%,  0px, 0px);`;
		const effectRight = `transform: translate3d(100%, 0px, 0px);`;
		links.forEach(link => {
			link.insertAdjacentHTML('beforeend', `<span class="hover">
		<span class="hover__text">${link.textContent}</span>
		</span>`);
			if (functions.isMobile.any()) {
				link.addEventListener('touchstart', menulinkActions);
				link.addEventListener('touchend', menulinkActions);
				document.documentElement.classList.add('touch')
			} else {
				link.onmouseenter = link.onmouseleave = menulinkActions;
				document.documentElement.classList.remove('touch')
			}
		});

		function menulinkActions(e) {
			const targetLink = e.target;
			const targetFirstSpan = targetLink.querySelector('.hover');
			const targetSecondSpan = targetLink.querySelector('.hover__text');
			const targetLinkHeight = targetLink.offsetWidth / 4;
			let getPageX = e.pageX || e.changedTouches[0].clientX;
			const targetLinkPos = getPageX - (targetLink.getBoundingClientRect().left + scrollX);
			if (e.type === 'mouseenter' || e.type === "touchstart") {
				// присваиваем спанам значения трансформа
				targetFirstSpan.style.cssText = targetLinkPos > targetLinkHeight ? effectRight : effectleft;
				targetSecondSpan.style.cssText = targetLinkPos > targetLinkHeight ? effectleft : effectRight;
				// меняем спанам значения трансформа
				setTimeout(() => {
					targetFirstSpan.style.cssText = effectHover + effectTransition;
					targetSecondSpan.style.cssText = effectHover + effectTransition;
				}, 5);
			}
			if (e.type === 'mouseleave' || e.type === "touchend") {
				targetFirstSpan.style.cssText = targetLinkPos > targetLinkHeight ? effectleft + effectTransition : effectRight + effectTransition;
				targetSecondSpan.style.cssText = targetLinkPos > targetLinkHeight ? effectRight + effectTransition : effectleft + effectTransition;
			}
		}
	}
	//filter
	functions.filter();
	//parallax
	let mainBg = document.querySelector('.mainblock__bg');
	mainBg ? startParallax(mainBg) : null
	function startParallax(mainBg) {
		document.addEventListener('scroll', () => parallax(mainBg));
	}
	function parallax(mainBg) {
		let scroll = window.scrollY;
		let windowWidthmax = Math.max(
			document.body.scrollWidth, document.documentElement.scrollWidth,
			document.body.offsetWidth, document.documentElement.offsetWidth,
			document.body.clientWidth, document.documentElement.clientWidth
		);
		let parallaxHeight = mainBg.offsetHeight;
		let perOfParallaxScroll = scroll / parallaxHeight * 100;
		let indexScale = 1 + (windowWidthmax / 100000 * perOfParallaxScroll);
		if (indexScale < 2.4) {
			mainBg.style.transform = `scale(${indexScale})`;
		} else {
			mainBg.style.removeProperty('transform');
			mainBg.removeEventListener('scroll', parallax);
		}
	}

});

const galleries = document.querySelectorAll('[data-gallery]');
if (galleries.length) {
	let galleyItems = [];
	galleries.forEach(gallery => {
		galleyItems.push({
			gallery,
			galleryClass: lightGallery(gallery, {
				// plugins: [lgZoom, lgThumbnail],
				licenseKey: '7EC452A9-0CFD441C-BD984C7C-17C8456E',
				speed: 500,
				controls: true,
				mobileSettings: {
					controls: true,
				}
			})
		})
	});
}