export const isMobile = {
	Android: function () { return navigator.userAgent.match(/Android/i); },
	BlackBerry: function () { return navigator.userAgent.match(/BlackBerry/i); },
	iOS: function () { return navigator.userAgent.match(/iPhone|iPad|iPod/i); },
	Opera: function () { return navigator.userAgent.match(/Opera Mini/i); },
	Windows: function () { return navigator.userAgent.match(/IEMobile/i); },
	any: function () { return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows()); }
};

export function isWebp() {
	// Проверка поддержки webp
	function testWebP(callback) {
		let webP = new Image();
		webP.src = "http://www.gstatic.com/webp/gallery/1.webp";

		webP.onload = function () {
			callback(webP.height > 0);
		};
		webP.onerror = function () {
			callback(false);
		};
	}
	// Добавление класса _webp или _no-webp для HTML
	testWebP((support) => {
		let className = support === true ? 'webp' : 'no-webp';
		document.documentElement.classList.add(className);
	});
}

export const chekIE = () => {
	let IE = false;
	//11
	const version11 = window.navigator.userAgent.indexOf('Trident/')
	//10-
	const version10 = window.navigator.userAgent.indexOf('MSIE ')
	if (version11 > -1 || version10 > -1) {
		IE = true;
		return IE;
	}
	return IE
}

export function fullScreenIE() {
	let IE = chekIE()
	const wrapper = document.querySelector('.wrapper');
	if (wrapper && IE) {
		wrapper.style.height = 100 + '%';
	}
}

export function burgerMenu() {
	const burgerButton = document.querySelector('.header__burger');
	const burgerMenu = document.querySelector('.header__menu');
	const headerPadding = document.querySelector('.about__header');
	if (burgerButton) {
		burgerButton.addEventListener('click', () => {
			headerPadding.classList.toggle('active')
			burgerButton.classList.toggle('active')
			_slideToggle(burgerMenu)
		})
	}
}

let _slideDown = (target, duration = 500) => {
	if (!target.classList.contains('slide')) {
		target.classList.add('slide', 'active');
		target.style.removeProperty('display');
		//блок проверки display на "none"
		let display = window.getComputedStyle(target).display;
		if (display === 'none')
			display = 'block';
		target.style.display = display;
		let height = target.offsetHeight;
		//без этого ствойства криво отображаются рамки при открытии
		target.style.overflow = 'hidden';
		//временно навешенные свойства для анимации
		target.style.height = 0;
		target.style.paddingTop = 0;
		target.style.paddingBottom = 0;
		target.style.marginTop = 0;
		target.style.marginBottom = 0;
		target.offsetHeight;
		target.style.transitionProperty = "height, margin, padding";
		target.style.transitionDuration = duration + 'ms';
		target.style.height = height + 'px';
		//удалаеят временно навешенные свойства для анимации. С задержкой нельзя, т.к.
		//срузу обнулятся padding and margen
		target.style.removeProperty('padding-top');
		target.style.removeProperty('padding-bottom');
		target.style.removeProperty('margin-top');
		target.style.removeProperty('margin-bottom');
		//Этот блок не обязателен, он удаляет навешенные свойства из консоли и из css
		window.setTimeout(() => {
			target.style.removeProperty('height');
			target.style.removeProperty('overflow');
			target.style.removeProperty('transition-duration');
			target.style.removeProperty('transition-property');
			target.classList.remove('slide');
		}, duration);
	}
}

//Закрывает спойлер
let _slideUp = (target, duration = 500) => {
	if (!target.classList.contains('slide')) {
		target.classList.add('slide', 'active');
		target.style.transitionProperty = 'height, margin, padding';
		target.style.transitionDuration = duration + 'ms';
		target.style.height = target.offsetHeight + 'px';
		target.offsetHeight;
		//без этого ствойства криво отображаются рамки при закрытии
		target.style.overflow = 'hidden';
		//временно навешенные свойства для анимации
		target.style.height = 0;
		target.style.paddingTop = 0;
		target.style.paddingBottom = 0;
		target.style.marginTop = 0;
		target.style.marginBottom = 0;
		//Этот блок не обязателен, он удаляет навешенные свойства из консоли и из css
		window.setTimeout(() => {
			target.style.display = 'none';
			// Эта строчка нужна, что бы убрать стрелку при аккордионе
			target.style.removeProperty('height');
			target.style.removeProperty('padding-top');
			target.style.removeProperty('padding-bottom');
			target.style.removeProperty('margin-top');
			target.style.removeProperty('margin-bottom');
			target.style.removeProperty('overflow');
			target.style.removeProperty('transition-duration');
			target.style.removeProperty('transition-property');
			target.classList.remove('slide', 'active');
		}, duration);
	}
}
let _slideToggle = (target, duration = 500) => {
	if (window.getComputedStyle(target).display === 'none') {
		return _slideDown(target, duration);
	} else {
		return _slideUp(target, duration);
	}
}

//filter
export function filter() {
	const links = document.querySelectorAll('.portfolio__link');
	links.forEach(link => {
		link.dataset.filter
		link.dataset.filter ? startfilter(link) : null
	})
	function startfilter(link) {
		const showCards = document.querySelectorAll('.portfolio__item');
		const opacityHide = `opacity:0; visibility:hidden; display:none;`
		const effectTransition = `transition: all 0.3s ease;`;
		link.addEventListener('click', (e) => {
			if (e.target.tagName !== 'LI') {
				return false
			}
			showCards.forEach(showCard => {
				showCard.style.opacity = 1;
				showCard.style.visibility = 'visible';
				showCard.style.display = 'block';
				if (!showCard.classList.contains(link.dataset.filter) && link.dataset.filter != 'all') {
					showCard.style.cssText = opacityHide + effectTransition;

				}
			})
		})
	}
}